import React, { useEffect, useState, useRef } from 'react';
const Project = ({ heading, data }) => {

    let x = {
        tags: ['js', 'HTML', 'CSS', 'ServiceWorker'],
        description: [
            `An offline to do list`,
            `Works by installing a service worker`,
            `Allows user to create,edit,update and delete entries`,
            `Update the entries in localstorage after every change.`,
            `Allow users to filter out entries based on active/completed status`,
            `Allow users to mark all entries as completed`,
            `Allow users to delete all completed entries`,
        ],
        github: 'http://github.com/rohanakar/todo',
        live: 'https://rohanakar.github.io/todo'
    }
    const [loaded, setLoaded] = useState(false);
    const image = useRef(null);
    const refreshIntervalId = useRef(null);
    const [hovered, setHovered] = useState(false);

    useEffect(() => {

        if (image.current && !refreshIntervalId.current)
            refreshIntervalId.current = setInterval(() => {
                if (image.current) {
                    setLoaded(image.current.complete)
                }
            }, 100);
    })
    if (loaded) {
        clearInterval(refreshIntervalId.current);
    }

    return (
        <section id='project' onMouseEnter={() => { setHovered(true) }}
            onMouseLeave={(e) => {
                setHovered(false);

            }}>
            <div className='img-container' style={loaded ? { opacity: 0 } : {}}>

            </div>
            <div className='img'
                style={{
                    background: data['fill'] ?
                        'radial-gradient(' + data["fill"] + ',transparent)' : 'linear-gradient(45deg,white,#efefef)',
                    filter: hovered ? 'blur(20px)' : ''
                }}>
                <img ref={image} src={'/Screenshot/' + heading + '/heading.jpg'} style={{ filter: 'drop-shadow(8px 8px 10px gray)' }} />
            </div>
            <div className='text-heading' style={hovered ? { opacity: 1, top: '40%' } : {}}>
                {heading}
            </div>
            <div className='know-more' id='button' style={hovered ? { opacity: 1, bottom: '25%' } : {}}>
                Learn more
            </div>
            <div className='tags'>

            </div>
        </section>
    )
}

export default Project;